import { notification } from 'antd';
import { Settings } from 'app/layout/antdPro/SettingDrawer';
import ImgLogo from 'assets/img/logo.png';
import ImgUser from 'assets/img/user.png';
import lodash from 'lodash';
import { configure, observable, toJS } from 'mobx';
import { create, persist } from 'mobx-persist';
import themeColor from 'utils/themeColor';
import Help from 'utils/Help';
import moment from 'moment';
// import 'moment/locale/zh-cn';
import "./global.less";
// const language = lodash.get(window, 'navigator.language', 'zh-CN');
// mobx  https://cn.mobx.js.org/refguide/api.html
configure({ enforceActions: "observed" });
notification.config({
    duration: 3,
    top: 60
});
const hydrate = create({
    storage: window.localStorage,   // 
    jsonify: true, //  json
    // debounce: 1000,
});
//   
const development = process.env.NODE_ENV === "development"
class ConfigStore {
    constructor() {
        hydrate('WTM_GlobalConfig', this)
            // post hydration
            .then(() => {
                console.log('WTM_GlobalConfig', toJS(this));
                // 
                themeColor.changeColor(this.settings.primaryColor);
                window['g_locale'] = this.language;
                // 
                moment.locale(lodash.toLower(this.language));
            })
    }
    buildTime = process.env.REACT_APP_TIME;
    /**
    * 
    */
    development = development;
    /**
     * 
     */
    default = {
        // title: "LQClass.Admin",
        logo: ImgLogo,
        avatar: ImgUser,
    };
    /**
     * 
     * @memberof ConfigStore
     */
    @persist
    @observable
    language: "zh-CN" | "en-US" = Help.GetNavigatorLanguage();
    /**
     * ant Pro    https://github.com/ant-design/ant-design-pro-layout/blob/master/README.zh-CN.md#MenuDataItem
     * @type {Settings}
     * @memberof ConfigStore
     */
    @persist("object")
    @observable
    settings: Settings = {
        //  'light' | 'dark'
        navTheme: 'dark',
        // layout ,sidemenutopmenu 'sidemenu' | 'topmenu'
        layout: 'sidemenu',
        // layout ,Fluid 1200pxFixed
        contentWidth: 'Fluid',
        primaryColor: "#1890FF",
        //  header 
        fixedHeader: true,
        //  header
        autoHideHeader: false,
        // 
        fixSiderbar: true,
        //  menu  locale,locale  menu 
        menu: {
            locale: true,
        },
        // layout    title
        title: 'WalkingTec MVVM',
        //  IconFont 
        iconfontUrl: '',
        // 
        infoType: "Modal",
        /**
        * AgGrid 
        * ag-theme-balham
        * ag-theme-material
        */
        agGridTheme: "ag-theme-balham",
        /**
         *  
         */
        tabsPage: true,
    }
    /**
     *  
     * process.env.NODE_ENV === "development"  
     */
    target = "/api";
    /**
     * 
     */
    getHeaders(headers?) {
        headers = lodash.merge({
            credentials: 'include',
            accept: "*/*",
            "Content-Type": "application/json",
            'Accept-Language': this.language,
            token: this.token.get()
        }, headers);
        console.log("ConfigStore -> getHeaders -> headers", headers)
        return headers
    };
    /**
     * token
     */
    token = {
        set(token) {
            window.localStorage.setItem('__token', token);
            return token
        },
        get() {
            return window.localStorage.getItem('__token') || null;
        },
        clear() {
            window.localStorage.removeItem("__User");
            //window.localStorage.clear();
            window.location.pathname = "/"
        }
    };
    /**      */
    // pageSizeOptions: ['10', '20', '30', '40', '50', '100', '200'],
    /**    */
    // @persist
    @observable
    Limit = 50;
    /** 
     *   
     */
    // @persist
    // @observable
    // infoType = "Modal";//Drawer || Modal
    /** 
    *   
    */
    // @persist
    @observable
    infoTypeWidth = '900px';
    /**
     *  item lable 
     * doc:https://ant.design/components/form-cn/
     */
    // @persist("object")
    @observable
    formItemLayout = {
        labelCol: {
            span: 6
        },
        wrapperCol: {
            span: 16
        },
    };
    /**
     *    24 
     */
    // @persist
    @observable
    infoColumnCount = 2;
    /**
    *    24 
    */
    // @persist
    @observable
    searchColumnCount = 3;
    /**
     * 
     */
    // @persist
    @observable
    lockingTableRoll = true;
    /**
     *  
     */
    // @persist
    @observable
    staticPage = "@StaticPage";
}
const GlobalConfig = new ConfigStore();

export default GlobalConfig
